.BurgerBackdrop {
  display: flex;
  position: fixed;
  left: 0;
  top: 60px;
  width: 100%;
  background-color: #fff;
  height: 100%;
}

.BurgerNavWrap {
  display: flex;
  position: fixed;
  left: 0;
  width: 95%;
  overflow: hidden;
  z-index: 1000;
  border-radius: 0px 12px 0px 0px;
  background: #162680;
  height: 100%;
  border-top: 1px solid #ccd0d6;
  border-right: 1px solid #ccd0d6;
  border-bottom: 1px solid #ccd0d6;
  box-shadow: 0px 0px 15px -7px rgba(0, 0, 0, 0.5);
  transition: right 0.3s ease-in-out;
}

.BurgerNav {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 25px 20px;
}
.burgerWrapper {
  position: relative;
}
.burgerWrapper::after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background-color: #fff;
}
.burgerFirstBlock,
.burgerSecondBlock,
.burgerThirdBlock {
  display: flex;
  flex-direction: column;
  padding: 15px;
}
.burgerNavLink {
  color: #fff;
  font-family: 'TWK600';
  font-size: 18px;
}

.blockTitle {
  color: #fff;
  font-family: 'TWK600';
  font-size: 30px;
  position: relative;
  padding: 15px;
}
.active {
  display: block;
  padding: 15px 15px 10px 15px;
}

.blockTitle::after {
  content: '';
  display: block;
  height: 20px;
  width: 18px;
  position: absolute;
  right: 6px;
  top: 50%;
  transform: translateY(-50%);
  background-image: url('../../icons/arrow-btm.svg');
  background-position: center;
  background-repeat: no-repeat;
  transition: transform 300ms ease-in-out;
}
.active::after {
  content: '';
  position: absolute;
  right: 6px;
  top: 50%;
  transform: translateY(-50%) rotate(180deg);
}
.burgerBlock {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 0px 20px 15px 20px;
}

.pinkText {
  color: #f948b4;
}
.orangeText {
  color: #fc5d1f;
}
.greenText {
  color: #00c82f;
}
.soon {
  display: block;
  width: fit-content;
  height: 100%;
  position: relative;
  color: #8a92c0;
}
.soon::after {
  content: 'soon';
  position: absolute;
  right: -33px;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 58px;
  background: #f948b4;
  display: flex;
  padding: 2px 3px 2px 3px;
  color: #fff;
  text-align: center;
  font-family: 'TWK600';
  font-size: 10px;
}
.burgerOrange .BurgerNavWrap {
  background-color: #fc5d1f;
}
.burgerOrange .BurgerNavWrap span {
  color: #fff;
}
.burgerOrange .BurgerNavWrap .soon::after {
  content: 'soon';
  color: #f6571b;
  background: #fadda0;
}

.burgerGreen .BurgerNavWrap {
  background-color: #00c82f;
}
.burgerGreen .BurgerNavWrap span {
  color: #fff;
}
.burgerGreen .BurgerNavWrap .soon::after {
  content: 'soon';
  color: #00c82f;
  background: #e8fabf;
}
